@import 'variables';



.header {
  height: 60px;
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $white-accent;
}

.logo {
  display: block;
  width: 40px;
  height: 40px;
}

.nav {
  height: inherit;
  margin: 0;


  &__list {
    margin: 0;
    padding: 0;
    height: inherit;
    display: flex;
    align-items: center;
    flex-direction: row;
  }

  &__item {
    display: flex;
    align-items: center;
    margin-right: 20px;
    height: 100%;
  }

  &__item:last-child {
    margin-right: 0;
  }

  &__link {
    display: flex;
    line-height: 60px;
    font-weight: 500;
    margin: 0;
    padding: 0;
    color: $main-accent;
    position: relative;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 12px;
    cursor: pointer;
  }

  &__link:hover {
    color: $blue-accent;
  }

  &__link:focus {
    color: $blue-accent;
  }
}

.is-active {
  color: $blue-accent;
}

.is-active::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  border-bottom: 4px $blue-accent solid;
  border-radius: 8px;
}
